import React from "react";
import styled from "styled-components";

const TicketWrapper = styled.div`
  background: #c6b497;
  padding: 20px;
  border-radius: 20px;

  &:not(:last-child) {
    margin-bottom: 5%;
  }
`;

const Title = styled.h3`
  width: 100%;
  margin: 0px;
`;

const Body = styled.p`
  width: 100%;
`;

const Ticket = ({ title, body }) => (
  <TicketWrapper>
    <Title>{title}</Title>
    <Body>{body}</Body>
  </TicketWrapper>
);

export default Ticket;
